<script lang="ts">
  import { createCapitalWords, updateHours } from "./utils";

  export let labelName: string = "";
  export let inputNameId: string;
  export let type: string = "text";
  export let placeholder: string = "";
  export let autocomplete: string = "off";
  export let errors: { [fieldName: string]: string };
  export let min: number = 0;
  export let max: number = 10000;
  export let minHours: boolean = false;
  export let value = null;

  let minHoursText: string = "0 minutes";
  if (labelName === "") labelName = createCapitalWords(inputNameId);
  if (placeholder === "") placeholder = createCapitalWords(inputNameId);
</script>

<label for={inputNameId}>{labelName}</label>

{#if type === "number"}
  <input
    {type}
    name={inputNameId}
    id={inputNameId}
    {min}
    {max}
    on:input={(e) => {
      errors[inputNameId] = "";
      if (minHours) minHoursText = updateHours(e);
    }}
    value={0}
  />
{:else if type === "date"}
  <input
    {type}
    name={inputNameId}
    id={inputNameId}
    on:input={() => (errors[inputNameId] = "")}
    {value}
  />
{:else if type === "textarea"}
  <textarea
    name={inputNameId}
    id={inputNameId}
    {placeholder}
    {autocomplete}
    on:input={() => (errors[inputNameId] = "")}
    {value}
  />
{:else}
  <input
    {type}
    name={inputNameId}
    id={inputNameId}
    {placeholder}
    {autocomplete}
    on:input={() => (errors[inputNameId] = "")}
    {value}
  />
{/if}

{#if minHours}
  <p class="minhours">{minHoursText}</p>
{/if}

{#if errors[inputNameId]}
  <p class="error-message">{errors[inputNameId]}</p>
{/if}

<style>
  .error-message,
  .minhours {
    flex: 0 0 100%;
    margin: 0 2px 10px 0;
    font-size: 0.8em;
  }
  .error-message {
    color: tomato;
  }
  textarea {
    width: 100%;
    height: 100px;
  }
</style>
