<script lang="ts">
  import AButton from "../../lib/AButton.svelte";
  import { arrows, days, updateDays } from "../../lib/agenda";
  import { api } from "../../lib/utils";

  const today = new Date();
  let currentMonth = today.getMonth();
  let currentYear = today.getFullYear();
  let currentDay = today.getDate();
  let updatedDays = updateDays(currentYear, currentMonth);
  let items = [];

  export let date = new Date(currentYear, currentMonth, currentDay);
  export let currentPage: string;

  const nexts = () => {
    currentMonth++;
    if (currentMonth > 11) {
      currentMonth = 0;
      currentYear++;
    }
    updatedDays = updateDays(currentYear, currentMonth);
  };

  const prevs = () => {
    currentMonth--;
    if (currentMonth < 0) {
      currentMonth = 11;
      currentYear--;
    }
    updatedDays = updateDays(currentYear, currentMonth);
  };

  const isToday = (day: number) => {
    const today = new Date();
    return (
      currentMonth === today.getMonth() &&
      currentYear === today.getFullYear() &&
      day === today.getDate()
    );
  };

  const setToday = () => {
    currentMonth = today.getMonth();
    currentYear = today.getFullYear();
    currentDay = today.getDate();
    updatedDays = updateDays(currentYear, currentMonth);
    date = new Date(currentYear, currentMonth, currentDay);
  };

  const showItems = async () => {
    console.log(date);
    items = await api().getItemsForDay(date);
    console.log(items);
  };
  showItems();
</script>

<div class="page-container">
  <div class="container">
    <div class="agenda">
      <div class="header">
        <AButton
          text={arrows.BACK}
          href="#"
          onClick={prevs}
          bgcolor="#000"
          hoverBgColor="#fff"
          hoverColor="#000"
        />
        <!-- <button on:click={prevs}>{arrows.BACK}</button> -->
        <div class="header-text">
          {currentMonth + 1}
          {currentYear}
        </div>
        <!-- <button on:click={nexts}>{arrows.NEXT}</button> -->
        <AButton
          text={arrows.NEXT}
          href="#"
          onClick={nexts}
          bgcolor="#000"
          hoverBgColor="#fff"
          hoverColor="#000"
        />
      </div>
      <div class="day-names">
        {#each days as day}
          <div class="day-name">{day[0]}</div>
        {/each}
      </div>

      <!-- Full grid with all days of the month -->
      <div class="days">
        <!-- grid -->
        {#each updatedDays as dayNumber}
          {@const thisDay = new Date(currentYear, currentMonth, dayNumber)}
          {#if dayNumber === 0}
            <div class="day" />
          {:else}
            <!-- svelte-ignore a11y-invalid-attribute -->
            <a
              class="day"
              class:today={isToday(dayNumber)}
              class:selected={date.getTime() === thisDay.getTime()}
              href="#"
              on:click={() => {
                date = thisDay;
                showItems();
              }}
            >
              {dayNumber}
            </a>
          {/if}
        {/each}
      </div>
    </div>
    <div class="appointment">OVER HERE ALL THE APPOINTMENT OF THAT DAY</div>
    <div class="button">
      <AButton
        text="Today"
        href="#"
        onClick={() => {
          setToday();
          showItems();
        }}
      />
      <div>
        <AButton
          text="Add appointment"
          href="#"
          onClick={() => (currentPage = "appointment")}
        />
        <AButton
          text="Contact"
          href="#"
          onClick={() => (currentPage = "contact")}
        />
      </div>
    </div>
  </div>
</div>

