export const updateHours = (e) => {
  const value = parseInt(e.target.value);
  if (value < 60) {
    return `${value} minutes`;
  } else {
    const hours = Math.floor(value / 60);
    return `${hours} hours and ${value % 60} minutes`;
  }
};

export function api() {
  const apiUrl = "http://0.0.0.0:42512/api";
  const headers = { "Content-Type": "application/json" };
  let response;
  return {
    getTypes: async () => {
      response = await fetch(`${apiUrl}/types`);
      return await response.json();
    },
    createItem: async (body) => {
      response = await fetch(`${apiUrl}/items`, {
        method: "POST",
        headers,
        body: JSON.stringify(body),
      });
      return await response.json();
    },
    getItemsForDay: async (date) => {
      response = await fetch(`${apiUrl}/items/${fullDate(date)}`);
      return await response.json();
    },
    getItems: async () => {
      response = await fetch(`${apiUrl}/items`);
      return await response.json();
    },
  };
}

export function createCapitalWords(scentence) {
  var words = scentence.split(" ");
  var newWords = [];
  for (var i = 0; i < words.length; i++) {
    newWords.push(words[i].charAt(0).toUpperCase() + words[i].slice(1));
  }
  return newWords.join(" ");
}

export function createDataErrors(e, errors, fullDay, multipleDays) {
  const data = {};
  for (const [key, value] of new FormData(e.target)) {
    if (!isRequiredFieldValid(value) && key !== "description")
      errors[key] = `${key[0].toUpperCase() + key.slice(1)} is required`;
    data[key] = value;
  }
  if (multipleDays) {
    if (data["endDate"] < data["date"])
      errors["endDate"] = "End date must be after start date";
  }
  if (!fullDay) {
    if (data["endTime"] < data["time"])
      errors["endTime"] = "End time must be after start time";
  }
  return { data, errors };
}

function isRequiredFieldValid(value) {
  return value != null && value !== "" && value !== "0";
}

export function fullDate(date) {
  return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
}
