<script lang="ts">
  import CheckBox from "../../lib/CheckBox.svelte";
  import Input from "../../lib/Input.svelte";
  import { api, createDataErrors } from "../../lib/utils";

  export let date: Date = new Date();
  export let currentPage: string;

  let multipleDays: boolean = false;
  let fullDay: boolean = false;
  let value: { id: number; name: string };
  let errors: { [fieldName: string]: string } = {};
  let repeat: boolean = false;
  let types = [];

  const onSubmit = async (e: any) => {
    const dataErrors = createDataErrors(e, errors, fullDay, multipleDays);
    errors = dataErrors.errors;
    const data = dataErrors.data;

    for (let key in errors) {
      if (errors[key] !== "") return;
    }

    await api().createItem(data);
    currentPage = "agenda";
  };

  const update = () => {
    errors = {};
    fullDay = false;
    multipleDays = false;
  };

  const getTypes = async () => {
    types = await api().getTypes();
  };
  getTypes();
</script>

<!-- scrollable div -->
<div class="appointment">
  <select bind:value on:change={update}>
    <option disabled selected value={0}> Select an option </option>
    {#each types as type}
      <option value={type}>{type.name}</option>
    {/each}
  </select>
  {#if value}
    <form on:submit|preventDefault={onSubmit}>
      <input type="text" name="type" id="type" value={value.id} hidden />
      <Input
        inputNameId="title"
        type="text"
        labelName="{value.name} title"
        placeholder="Title"
        {errors}
      />

      {#if value.id === 2}
        <Input inputNameId="season" type="number" {errors} />
        <Input inputNameId="episode" type="number" {errors} />
      {/if}
      <Input
        inputNameId="minutes"
        type="number"
        labelName="Minutes in {value.name}"
        {errors}
        minHours={true}
      />
      <Input type="textarea" inputNameId="description" {errors} />
      <CheckBox
        bind:checked={multipleDays}
        id="multipleDays"
        name="Multiple days"
        onChange={() => (errors["endDate"] = "")}
      />
      <input
        type="text"
        name="multipleDays"
        id="multipleDays"
        value={multipleDays}
        hidden
      />
      <CheckBox
        bind:checked={fullDay}
        id="fullDay"
        name="Full day"
        onChange={() => {
          errors["time"] = "";
          errors["endTime"] = "";
        }}
      />
      <input type="text" name="fullDay" id="fullDay" value={fullDay} hidden />
      <Input
        inputNameId="date"
        type="date"
        labelName={multipleDays ? "Start date" : "Date"}
        value={date}
        {errors}
      />
      {#if !fullDay}
        <Input inputNameId="time" type="time" {errors} />
      {/if}
      {#if multipleDays}
        <Input
          inputNameId="endDate"
          type="date"
          labelName="End date"
          {errors}
        />
      {/if}
      {#if !fullDay}
        <Input
          inputNameId="endTime"
          type="time"
          labelName="End time"
          {errors}
        />
      {/if}
      {#if !multipleDays}
        <CheckBox
          bind:checked={repeat}
          id="repeat"
          name="Repeat"
          onChange={() => (errors["repeat"] = "")}
        />
        <input type="text" name="repeat" id="repeat" value={repeat} hidden />
        {#if repeat}
          <select name="repeatType" id="repeatType">
            <option value="daily">Daily</option>
            <option value="weekly">Weekly</option>
            <option value="monthly">Monthly</option>
            <option value="yearly">Yearly</option>
          </select>
        {/if}
      {/if}

      <button type="submit">Add Appointment</button>
    </form>
  {/if}
</div>

<style>
  select {
    width: 100%;
    margin-bottom: 10px;
  }

  form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
  }

  .appointment {
    height: 80vh;
    overflow-y: auto;
    padding: 0 10px;
  }
</style>
